import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import React from 'react';
import Scrollbar from 'react-scrollbars-custom';
import { baseAnimation, ColorPalette, materialAnimation } from '../../utils/StyleGuide';

const animateScrollbar = `opacity ${baseAnimation}`;

const styles = {
  scrollWrap: {
    width: '100%',
    overflow: 'hidden',
    position: 'absolute',
  },
  scrollbarThumb: {
    background: `${ColorPalette.passiveDarkGrey}!important`,
    borderRadius: '7.5px!important',
    display: 'block',
  },
  scrollbarTrack: {
    background: `${ColorPalette.midGrey}!important`,
    borderRadius: '7.5px!important',
    display: 'block',
    // top is being set via style
  },
};

class ScrollBar extends React.Component {
  state = {
    showScrollbar: false,
    dontHideScrollbar: false,
    scrollPos: 0,
  };

  isAutoScrolling = false;

  autoScrollTarget = 0;

  scrollbar = null;

  scrollTrackRef = null;

  actualScrollPosition = 0;

  previousScrollPosition = -1;

  bufferScrollTopTarget = null;

  bufferContentKey = '';

  isScrollbarOn = false;

  componentDidMount() {
    document.addEventListener('mouseup', this.scrollbarMouseUp);
    document.addEventListener('mousedown', this.scrollbarMouseDown);
  }

  componentWillUnmount() {
    document.removeEventListener('mouseup', this.scrollbarMouseUp);
    document.removeEventListener('mousedown', this.scrollbarMouseDown);
  }

  scrollbarMouseUp = () => {
    this.setState({ dontHideScrollbar: false });
  };

  scrollbarMouseDown = () => {
    const { showScrollbar } = this.state;
    if (showScrollbar) {
      this.setState({ dontHideScrollbar: true });
    }
  };

  startAutoScroll = target => {
    this.isAutoScrolling = true;
    this.autoScrollTarget = target;
    this.previousScrollPosition = -1;
    window.requestAnimationFrame(this.autoScroll);
  };

  autoScroll = () => {
    if (this.isAutoScrolling && this.scrollbar) {
      let scrollPos = -1;

      if (Math.abs(this.autoScrollTarget - this.actualScrollPosition) < 4) {
        scrollPos = this.autoScrollTarget;
      } else {
        const scrollDifference =
          (this.autoScrollTarget - this.actualScrollPosition) * 0.3;
        scrollPos = this.actualScrollPosition + scrollDifference;
      }

      if (scrollPos >= 0) {
        this.actualScrollPosition = scrollPos;
        this.setState({
          scrollPos: scrollPos,
        });
      }
      if (this.previousScrollPosition === this.actualScrollPosition) {
        this.isAutoScrolling = false;
      }
      this.previousScrollPosition = this.actualScrollPosition;
      window.requestAnimationFrame(this.autoScroll);
    }
  };

  onScroll = e => {
    const { onScrollCallback } = this.props;
    if (onScrollCallback) {
      onScrollCallback(e);
    }
    if (!this.isAutoScrolling) {
      this.actualScrollPosition = e.scrollTop;
    }
  };

  onMouseEnter = () => {
    const { showScrollbar } = this.state;
    const { onRollOver } = this.props;
    if (!showScrollbar) {
      this.setState({ showScrollbar: true });
      if (onRollOver) {
        onRollOver();
      }
    }
  };

  onMouseLeave = () => {
    const { onRollOut } = this.props;
    this.setState({ showScrollbar: false });
    if (onRollOut) {
      onRollOut();
    }
  };

  onScrollUpdate = () => {
    const { onTrackOn, onTrackOff } = this.props;
    // this is not very nice as it connects css and logic
    // but disableTracksWidthCompensation is not interactive on hover
    // and handling this functionality in the parent object was unsuccessful
    if (this.scrollTrackRef) {
      if (!this.isScrollbarOn && this.scrollTrackRef.style.display !== 'none') {
        this.isScrollbarOn = true;
        if (onTrackOn) {
          onTrackOn();
        }
      }
      if (this.isScrollbarOn && this.scrollTrackRef.style.display === 'none') {
        this.isScrollbarOn = false;
        if (onTrackOff) {
          onTrackOff();
        }
      }
    }
  };

  render() {
    const {
      classes,
      children,
      width,
      height,
      maxHeight,
      autoScroll,
      scrollTopTarget,
      topGap,
      trackWidth,
      trackHeightGap,
      contentKey,
      noScrollX,
      noScrollY,
      addTracksCompensation,
    } = this.props;
    const { showScrollbar, dontHideScrollbar, scrollPos } = this.state;

    let scrollTop;
    if (this.isAutoScrolling) {
      scrollTop = scrollPos;
    }

    // if auto scroll is allowed and have a different scrollTopTarget than before
    if (
      autoScroll &&
      (scrollTopTarget || scrollTopTarget === 0) &&
      scrollTopTarget !== this.bufferScrollTopTarget
    ) {
      this.bufferScrollTopTarget = scrollTopTarget;
      this.startAutoScroll(scrollTopTarget);
    }

    // if scrollTopTarget is resetted outside -> register it in the buffer
    if (scrollTopTarget !== this.bufferScrollTopTarget) {
      this.bufferScrollTopTarget = scrollTopTarget;
    }

    if (this.bufferContentKey !== contentKey) {
      this.bufferContentKey = contentKey;
      scrollTop = 0;
    }

    if (this.scrollTrackRef) {
      this.scrollTrackRef.style.transition = animateScrollbar;
      this.scrollTrackRef.style.opacity = showScrollbar || dontHideScrollbar ? 1 : 0;
    }

    return (
      <Scrollbar
        scrollTop={scrollTop}
        className={classes.scrollWrap}
        onScroll={this.onScroll}
        onUpdate={this.onScrollUpdate}
        style={{
          height,
          width,
          maxHeight,
          transition: `width ${materialAnimation}`,
        }}
        onMouseLeave={this.onMouseLeave}
        onMouseMove={this.onMouseEnter}
        disableTracksWidthCompensation={!addTracksCompensation}
        ref={ref => {
          this.scrollbar = ref;
        }}
        thumbYProps={{
          renderer: props => {
            const { elementRef, ...restProps } = props;
            return (
              <span
                {...restProps}
                ref={elementRef}
                className={classes.scrollbarThumb}
                style={{
                  ...restProps.style,
                  width: trackWidth,
                }}
              />
            );
          },
        }}
        trackYProps={{
          renderer: props => {
            const { trackMargin } = this.props;
            const { elementRef,  ...restProps } = props;
            
            return (
              <span
                {...restProps}
                ref={ref => {
                  this.scrollTrackRef = ref;
                  elementRef(ref);
                }}
                className={classes.scrollbarTrack}
                style={{
                  ...restProps.style,
                  top: `${topGap}px`,
                  width: trackWidth,
                  height: `calc(100% - ${trackHeightGap}px)`,
                  margin: trackMargin || 0,
                }}
              />
            );
          },
        }}
        noScrollX={noScrollX}
        noScrollY={noScrollY}
      >
        {children}
      </Scrollbar>
    );
  }
}

ScrollBar.propTypes = {
  classes: PropTypes.shape({}).isRequired,
  children: PropTypes.element,
  width: PropTypes.string,
  height: PropTypes.string,
  maxHeight: PropTypes.string,
  onScrollCallback: PropTypes.func,
  autoScroll: PropTypes.bool,
  scrollTopTarget: PropTypes.number,
  topGap: PropTypes.number,
  trackWidth: PropTypes.number,
  trackHeightGap: PropTypes.number,
  contentKey: PropTypes.string, // one can force to jump scroll to the top by changing this key.
  noScrollX: PropTypes.bool,
  noScrollY: PropTypes.bool,
  addTracksCompensation: PropTypes.bool,
  onRollOver: PropTypes.func,
  onRollOut: PropTypes.func,
  onTrackOn: PropTypes.func,
  onTrackOff: PropTypes.func,
  trackMargin: PropTypes.number,
};
ScrollBar.defaultProps = {
  children: null,
  width: '100%',
  height: '100%',
  maxHeight: '100%',
  onScrollCallback: null,
  autoScroll: false,
  scrollTopTarget: 0,
  topGap: 3,
  trackWidth: 16,
  trackHeightGap: 20,
  contentKey: '',
  noScrollX: false,
  noScrollY: false,
  addTracksCompensation: false,
  trackMargin: 0,
};
export default withStyles({ ...styles })(ScrollBar);
