export const convertCSVToJSON = (str: string, delimiter = ',') => {
  const titles = str.slice(0, str.indexOf('\n')).split(delimiter);
  const rows = str.slice(str.indexOf('\n') + 1).split('\n');
  return { titles, rows: rows.map((row: any) => row.split(delimiter)) };
};
