import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components/macro';
import { ANIMATION_DIRECTION, MenuConfig } from '../types';
import DashMenuPanel from './DashMenuPanel';
import { activeMenuWidth, passiveMenuWidth } from '../dashUtils';

const MenuWrapper = styled.div`
  display: flex;
  position: absolute;
  top: 0;
  height: 100%;
  box-shadow: 0 2px 12px 0 rgba(83, 97, 255, 0.52);
`;
interface Props {
  hasSubMenu: boolean;
  mainMenuSetup: MenuConfig;
  subMenuSetup: MenuConfig;
  toggleCallback: () => void;
}

const DashMenu = ({ hasSubMenu, mainMenuSetup, subMenuSetup, toggleCallback }: Props) => {
  const [mainMenuHover, setMainMenuHover] = useState(false);
  const [hover, setHover] = useState(false);
  const [allowOpen, setAllowOpen] = useState(false);
  const openTimeout = useRef(0);

  const onRollOut = () => {
    setHover(false);
    setAllowOpen(false);
    clearTimeout(openTimeout.current);
  };

  const onRollOver = () => {
    let checkTimeout = hover;
    setHover(true);
    openTimeout.current = setTimeout(() => setAllowOpen(true), 1000);
    /*if (!hover) {
      setHover(true);
      checkTimeout = true;
    }
    if (checkTimeout) {
      clearTimeout(openTimeout.current);
      openTimeout.current = setTimeout(() => setAllowOpen(true), 52);
    }*/
  };

  return (
    <MenuWrapper onMouseEnter={onRollOver} onMouseLeave={onRollOut}>
      <DashMenuPanel
        id="sub-menu"
        key="sub-menu"
        show={hasSubMenu}
        side={ANIMATION_DIRECTION.LEFT}
        setup={subMenuSetup.content}
        active={subMenuSetup.active}
        position={{
          x: passiveMenuWidth,
          y: 0,
        }}
        callback={subMenuSetup.callback}
        autoOpen={true}
        allowOpen={allowOpen}
      />
      <DashMenuPanel
        id="main-menu"
        key="main-menu"
        show={true}
        side={ANIMATION_DIRECTION.LEFT}
        setup={mainMenuSetup.content}
        active={mainMenuSetup.active}
        isDarkMode={true}
        isClosable={true}
        showToggleButton={false}
        toggleCallback={toggleCallback}
        callback={mainMenuSetup.callback}
        setMenuHover={(hover: boolean) => setMainMenuHover(hover)}
        allowOpen={allowOpen}
      />
    </MenuWrapper>
  );
};

export default DashMenu;
